import React from 'react';

interface Props {
    images: Array<string>;
}

interface State {
    images: Array<string>;
    index: number;
}

export default class ImageCarousal extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {images: props.images, index: 0};
    }

    moveLeft() {
        if (this.state.index === 0) {
            this.setState({index: this.state.images.length - 1});
        }
    }

    moveRight() {
        this.index += 1;
        if (this.index == this.images.length) {
            this.index = 0;
        }
    }

    render() {
        return (
            <div className='sculpture-photo-container'>
                <img className='sculpture-photo' src={this.images[this.index]} />
                <div className="carousal-controls-container">
                    <div onClick={this.moveLeft}>&#8592;</div>
                    <a>&#8594;</a>
                </div>
            </div>
        );
    }
};